.sidebar {
    position: sticky;
    top: 80px;
    flex: 0.3;
    border-radius: 10px;
    text-align: center;
    height: fit-content;
}

.sidebar__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgrey;
    border-bottom: 0;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}

.sidebar__top > h4 {
    color: grey;
    font-size: 10px;
}

.sidebar__top > h2 {
    font-size: 16px;
}

.sidebar__top > img {
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
}

.sidebar__bottom, .sidebar__status {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgrey;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar__avatar {
    margin-bottom: 10px;
}

.sidebar__status {
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.sidebar__stat > p {
    color: gray;
    font-size: 13px;
    font-weight: 600;
}

.sidebar__statNumber {
    font-weight: bold;
    color: #0a66c2;
}

.sidebar__recentItem {
    display: flex;
    font-size: 13px;
    color: grey; /* Corrected from 'font-size' to 'color' */
    font-weight: bolder;
    cursor: pointer;
    margin-bottom: 5px;
    padding: 5px;
}

.sidebar__recentItem:hover {
    background-color: whitesmoke;
    border-radius: 10px;
    color: black;
}

.sidebar__hash {
    margin-right: 10px;
    margin-left: 5px;
}